<template>
  <section>
    <h3>{{actionName}}</h3>
    <rate-slicer
      v-if="languages.length"
      :sliders="languages"
      :tip="''"
      @updated="updatedFirst"
    ></rate-slicer>
    <rate-slicer
      v-if="languages.length"
      :sliders="languages"
      :tip="''"
      @updated="updatedRead"
    ></rate-slicer>
    <rate-slicer
      v-if="languages.length"
      :sliders="languages"
      :tip="'<b></b>'"
      @updated="updatedSpeak"
    ></rate-slicer>
    <rate-slicer
      v-if="languages.length"
      :sliders="languages"
      :tip="'<b></b>'"
      @updated="updatedWrite"
    ></rate-slicer>
  </section>
</template>

<script>
import RateSlider from './RateSlider'

export default {
  name: 'RateSelector',
  props: ['languages'],
  data: function () {
    return {
      results: {
        first: {},
        read: {},
        speak: {},
        write: {}
      }
    }
  },
  methods: {
    updatedFirst: function (data) {
      this.results.first = data
      this.update()
    },
    updatedRead: function (data) {
      this.results.read = data
      this.update()
    },
    updatedSpeak: function (data) {
      this.results.speak = data
      this.update()
    },
    updatedWrite: function (data) {
      this.results.write = data
      this.update()
    },
    update: function () {
      this.$emit('updated', this.results)
    }
  },
  computed: {
    actionName: function () {
      if (window.document.body.clientWidth < 960) {
        return ''
      } else {
        return ''
      }
    }
  },
  components: {
    'rate-slicer': RateSlider
  }
}
</script>


