<template>
  <Form :model="info" :label-width="200">
    <FormItem label="">
      <Row :gutter="16">
        <Col span="9">
          <Input v-model="info.name" placeholder=""></Input>
        </Col>
        <Col span="9">
          <Input v-model="info.code" placeholder=""></Input>
        </Col>
      </Row>
    </FormItem>

    <FormItem label="/">
      <Row :gutter="16">
        <Col span="9">
          <DatePicker type="date" style="width:100%;" v-model="info.birthday" placeholder=""></DatePicker>
        </Col>
        <Col span="9">
          <InputNumber v-model="info.age" style="width:100%;" :min="10" :max="30"></InputNumber>
        </Col>
      </Row>
    </FormItem>

    <FormItem label="">
      <Row :gutter="16">
        <Col span="18">
          <RadioGroup style="float: left;" v-model="info.gender">
            <Radio label="0"></Radio>
            <Radio label="1"></Radio>
          </RadioGroup>
        </Col>
      </Row>
    </FormItem>

    <FormItem label="">
      <Row :gutter="16">
        <Col span="6">
          <Input v-model="info.nation" placeholder=""></Input>
        </Col>
        <Col span="6">
          <Input v-model="info.province" placeholder=""></Input>
        </Col>
        <Col span="6">
          <Input v-model="info.city" placeholder=""></Input>
        </Col>
      </Row>
    </FormItem>

    <FormItem label="">
      <Row :gutter="16">
        <Col span="9">
          <Input v-model="info.university" placeholder=""></Input>
        </Col>
        <Col span="9">
          <Input v-model="info.college" placeholder=""></Input>
        </Col>
      </Row>
    </FormItem>

    <FormItem label="">
      <Row :gutter="16">
        <Col span="9">
          <Input v-model="info.major" placeholder=""></Input>
        </Col>
        <Col span="9">
          <Input v-model="info.studentnumber" placeholder=""></Input>
        </Col>
      </Row>
    </FormItem>

    <FormItem label="">
      <Row :gutter="16">
        <Col span="6">
          <Input v-model="info.phone" placeholder=""></Input>
        </Col>
        <Col span="6">
          <Input v-model="info.qq" placeholder="QQ"></Input>
        </Col>
        <Col span="6">
          <Input v-model="info.wechat" placeholder=""></Input>
        </Col>
      </Row>
    </FormItem>
  </Form>
</template>

<script>
export default {
  name: 'InfoForm',
  props: ['info'],
  methods: {
    calcAge: function () {
      if (this.info) {
        const today = (new Date()).setHours(0)
        const ageMS = today - this.info.birthday
        this.info.age = (new Date(ageMS)).getYear() - 70
      }
    }
  },
  watch: {
    'info.birthday': function () {
      this.calcAge()
    }
  },
  mounted: function () {
    this.calcAge()
  }
}
</script>

<style>
.ivu-select-item {
  display: block;
}
</style>


