<template>
<div>
  <score-section
    :title="''"
    :section="level"
    :names="levelNames"
    :languages="languages"
    :legends="levelLegends"
    @updated="updated"
  ></score-section>
  <score-section
    :title="''"
    :section="impact"
    :names="impactNames"
    :languages="languages"
    :legends="impactLegends"
    @updated="updated"
  ></score-section>
  <score-section
    :title="''"
    :section="touch"
    :names="touchNames"
    :languages="languages"
    :legends="rateLegends"
    @updated="updated"
  ></score-section>
  <score-section
    :title="''"
    :section="oral"
    :names="oralNames"
    :languages="otherLanguages"
    :legends="rateLegends"
    @updated="updated"
  ></score-section>
</div>
</template>
<script>
import * as _ from 'lodash'
import ScoreSection from './ScoreSection'

export default {
  name: 'ScoreTable',
  props: ['languages'],
  data: function () {
    return {
      levelLegends: ['', '', '', '', '', '', '', '', '', '', ''],
      impactLegends: ['', '', '', '', '', '', '', '', '', '', ''],
      rateLegends: ['', '', '', '', '', '', '', '', '', '', ''],
      levelNames: {
        suffix: '',
        speak: '',
        listen: '',
        read: ''
      },
      contextNames: {
        family: '',
        friend: '',
        school: '',
        broadcast: '',
        read: '',
        tv: '',
        network: '/',
        social: 'QQ'
      },
      level: {
        speak: {},
        listen: {},
        read: {}
      },
      impact: {
        family: {},
        friend: {},
        school: {},
        broadcast: {},
        read: {},
        tv: {},
        network: {},
        social: {}
      },
      touch: {
        family: {},
        friend: {},
        school: {},
        broadcast: {},
        read: {},
        tv: {},
        network: {},
        social: {}
      },
      oral: {
        speak: {},
        listen: {}
      }
    }
  },
  methods: {
    updated: function () {
      this.$emit('updated', {
        level: _.clone(this.level),
        impact: _.clone(this.impact),
        touch: _.clone(this.touch),
        oral: _.clone(this.oral)
      })
    }
  },
  watch: {
    languages: function (languages) {
      const init = {}
      _.each(languages, lang => {
        init[lang.id] = 0
      })

      this.level.speak = _.clone(init)
      this.level.listen = _.clone(init)
      this.level.read = _.clone(init)

      this.impact.family = _.clone(init)
      this.impact.friend = _.clone(init)
      this.impact.school = _.clone(init)
      this.impact.broadcast = _.clone(init)
      this.impact.read = _.clone(init)
      this.impact.tv = _.clone(init)
      this.impact.network = _.clone(init)
      this.impact.social = _.clone(init)

      this.touch.family = _.clone(init)
      this.touch.friend = _.clone(init)
      this.touch.school = _.clone(init)
      this.touch.broadcast = _.clone(init)
      this.touch.read = _.clone(init)
      this.touch.tv = _.clone(init)
      this.touch.network = _.clone(init)
      this.touch.social = _.clone(init)

      const initOthers = {}
      _.each(this.otherLanguages, lang => {
        initOthers[lang.id] = 0
      })
      this.oral.speak = _.clone(initOthers)
      this.oral.listen = _.clone(initOthers)
      this.updated()
    }
  },
  computed: {
    impactNames: function () {
      var names = _.clone(this.contextNames)
      names.suffix = ''
      return names
    },
    touchNames: function () {
      var names = _.clone(this.contextNames)
      names.suffix = ''
      return names
    },
    otherLanguages: function () {
      return this.languages.slice(1)
    },
    oralNames: function () {
      const languagesText = _.map(this.languages, lang => lang.text)
      const text = '' + languagesText.slice(1).join('') + '' + languagesText[0] + ''
      return {
        speak: '',
        listen: text
      }
    }
  },
  components: {
    'score-section': ScoreSection
  }
}
</script>

<style>
  .rate-label {
    display: inline-block;
    padding-left: 50px;
    width: 150px;
  }
</style>


